import React, { useState, useEffect } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Checkbox,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	useTheme,
	IconButton,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql, useLazyQuery } from '@apollo/client';
import { useHistory } from 'react-router';
import { generatePath } from 'react-router-dom';
import { TProperty, TSearchFilterForm } from '../../../types';
import graphql from '../../../graphql';
import { getComparator, stableSort } from '../../../utils/table';
import Loader from '../../../ui-component/Loader';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import config from '../../../config';
import { useOrder, useSelectRow } from '../../../hooks';
import PropertyToolbar from './PropertyToolbar';
import { formatDate } from '../../../helpers/date';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

const PropertyList = () => {
	const { loading, error, data, fetchMore } = useQuery(
		gql(graphql.listProperties),
		{
			variables: { limit: config.pagination.limit },
			fetchPolicy: 'cache-first',
			notifyOnNetworkStatusChange: true,
		}
	);

	const {
		loading: loadingStatuses,
		error: statusesError,
		data: statusesData,
	} = useQuery(gql(graphql.listPropertyStatuses));

	const [doSearch, setDoSearch] = useState(false);
	const [executeSearch, { loading: isSearching, data: searchData }] =
		useLazyQuery(gql(graphql.searchProperties));

	const [rows, setRows] = useState<TProperty[]>([]);

	useEffect(() => {
		let newRows = [];
		if (doSearch && !isSearching)
			newRows = _get(searchData, 'searchProperties', []);
		if (!doSearch) newRows = _get(data, 'listProperties.items', []);
		setRows(newRows);
	}, [data, searchData, doSearch, isSearching]);

	const nextToken: string | null | undefined = _get(
		data,
		'listProperties.nextToken',
		undefined
	);
	const theme = useTheme();
	const classes = useStyles();
	const history = useHistory();
	const { order, orderBy, handleRequestSort } = useOrder('CreatedAt');
	const { selected, isSelected, handleSelect, handleSelectAll } =
		useSelectRow('PropertyId');

	if ((loading || loadingStatuses) && !data) return <Loader />;
	if (error || statusesError) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Something went wrong!
			</Alert>
		);
	}

	const loadMore = () => {
		fetchMore({
			variables: { limit: config.pagination.limit, nextToken },
		});
	};

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, rows);
	};

	const onCreateHandler = () => {
		const propertyAddNewLink = generatePath('/property-add');
		history.push(propertyAddNewLink);
	};

	const editProperty = (id: string) => {
		history.push(`/property/${id}`);
	};

	const extractPropertyData = (row: TProperty) => {
		const propertyData = _get(row, 'PropertyData', {});
		const streetAddress = _get(propertyData, 'StreetAddress', '');
		const county = _get(propertyData, 'County', '');
		const zip = _get(propertyData, 'Zip', '');
		const city = _get(propertyData, 'City', '');
		const state = _get(propertyData, 'State', '');
		const propertyNo = _get(row, 'PropertyNo', '');
		const createdAt = _get(row, 'CreatedAt', '');
		return { streetAddress, county, zip, city, state, propertyNo, createdAt };
	};

	const searchHandler = (values: TSearchFilterForm) => {
		const { search, statuses } = values;
		executeSearch({
			variables: {
				SearchText: search,
				StatusIds: _isEmpty(statuses) ? undefined : statuses,
			},
		});
	};

	const statuses = _get(statusesData, ['listPropertyStatuses'], []);

	return (
		<>
			<PropertyToolbar
				numSelected={selected.length}
				onCreate={onCreateHandler}
				searchHandler={searchHandler}
				triggerSearch={setDoSearch}
				statuses={statuses}
			/>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header
						numSelected={selected.length}
						order={order}
						orderBy={orderBy}
						onSelectAllClick={handleSelectAllClick}
						onRequestSort={handleRequestSort}
						rowCount={rows.length}
					/>
					<TableBody>
						{stableSort<TProperty>(rows, getComparator(order, orderBy)).map(
							(row, index) => {
								if (typeof row === 'number') return null;
								const isItemSelected = isSelected(row.PropertyId);
								const labelId = `enhanced-table-checkbox-${index}`;
								const {
									streetAddress,
									county,
									zip,
									city,
									state,
									propertyNo,
									createdAt,
								} = extractPropertyData(row);
								return (
									<TableRow
										hover
										onClick={() => handleSelect(row.PropertyId)}
										role='checkbox'
										aria-checked={isItemSelected}
										tabIndex={-1}
										key={row.PropertyId}
										selected={isItemSelected}
									>
										<TableCell sx={{ pl: 3 }} padding='checkbox'>
											<Checkbox
												color='primary'
												checked={isItemSelected}
												inputProps={{
													'aria-labelledby': labelId,
												}}
											/>
										</TableCell>
										<TableCell align='left'>{propertyNo}</TableCell>
										<TableCell align='left'>{streetAddress}</TableCell>
										<TableCell align='left'>{county}</TableCell>
										<TableCell align='left'>{state}</TableCell>
										<TableCell align='left'>{zip}</TableCell>
										<TableCell align='left'>{city}</TableCell>
										<TableCell align='left'>{formatDate(createdAt)}</TableCell>
										<TableCell align='center' sx={{ pr: 3 }}>
											<IconButton
												color='primary'
												onClick={() => editProperty(row.PropertyId)}
											>
												<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
											</IconButton>
										</TableCell>
									</TableRow>
								);
							}
						)}
					</TableBody>
				</Table>
			</TableContainer>
			<LoadMore onLoadMore={loadMore} disabled={!nextToken} loading={loading} />
		</>
	);
};

export default PropertyList;
