import { alpha, makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Fab, Grid, Toolbar, Tooltip, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/AddTwoTone';
import {
	EnhancedTableToolbarWithMassActionProps,
	TSearchFilterForm,
} from '../../../types';
import SearchFilter from '../components/SearchFilter';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(2),
		height: '83px',
	},
	highlight: {
		backgroundColor: alpha(
			theme.palette.primary.main,
			theme.palette.action.activatedOpacity
		),
	},
	title: {
		flex: '1 1 100%',
	},
}));

type Props = EnhancedTableToolbarWithMassActionProps & {
	searchHandler: (values: TSearchFilterForm) => void;
	triggerSearch: (doSearch: boolean) => void;
	statuses: Array<Object>;
};

const PropertyToolbar = (props: Props) => {
	const classes = useToolbarStyles();
	const { numSelected, onCreate, searchHandler, triggerSearch, statuses } =
		props;

	return (
		<>
			{!numSelected && (
				<Toolbar className={clsx(classes.root)}>
					<SearchFilter
						onSearch={searchHandler}
						onTriggerSearch={triggerSearch}
						statuses={statuses}
					/>
				</Toolbar>
			)}
			<Toolbar className={clsx(classes.root)}>
				<Grid
					container
					justifyContent='space-between'
					alignItems='center'
					spacing={2}
					className={clsx({
						[classes.highlight]: numSelected > 0,
					})}
				>
					<Grid item xs={12} sm={6}>
						{numSelected > 0 && (
							<Typography
								className={classes.title}
								color='inherit'
								variant='subtitle1'
								component='div'
							>
								{numSelected} selected
							</Typography>
						)}
					</Grid>
					<Grid item xs={12} sm={6}>
						<Grid container justifyContent='flex-end' alignItems='center'>
							<Tooltip title='Create'>
								<Fab
									color='primary'
									aria-label='add'
									sx={{
										boxShadow: 'none',
										ml: 1,
										width: '48px',
										height: '48px',
									}}
									onClick={onCreate}
								>
									<AddIcon />
								</Fab>
							</Tooltip>
						</Grid>
					</Grid>
				</Grid>
			</Toolbar>
		</>
	);
};

export default PropertyToolbar;
